import { Link } from "react-router-dom";

function NewsCard({ imgUrl, date, title, id, fullInfo, aos, aosDelay }) {
  return (
    <div
      className="col-lg-4 col-md-6 col-12 mb-md-5 mb-4 news-card"
      data-aos={aos}
      data-aos-delay={aosDelay}
      data-aos-duration="800"
      data-aos-once="true"
    >
      <div className="img-box">
        <img src={imgUrl} alt={title} />
      </div>
      <div className="text-box">
        <p className="date">{date}</p>
        <h3>{title}</h3>
        <p className="card-text">{fullInfo.substring(0, 238)}...</p>
        <div className="text-end">
          <Link
            className="effect effect-1"
            to={"/news/" + id + "/" + title.replace(/\s/gi, "-").toLowerCase()}
          >
            Batafsil
          </Link>
        </div>
      </div>
    </div>
  );
}

export default NewsCard;
